// Layout

$layout-bounds: 1080px;
$gutter-width: 30px;

$mobile-breakpoint: 640px;
$tablet-breakpoint: 1200px;

// Type

$display-font: "Work Sans", sans-serif;
$body-font: "Cousine";

$headers: h1, h2, h3, h4, h5, h6;
$headers-array: $headers;

$h1: 3.8rem;
$h2: 2.4rem;
$h3: 1.8rem;
$h4: 1.6rem;
$h5: 1.4rem;
$h6: 1.4rem;

// Colors

$display-color: #222222;
$body-color: #666666;

$primary: #689b98;
$secondary: #7c689b;

$link: $primary;
$link-hover: darken($link, 10%);
$link-secondary: lighten($link, 20%);

$color-inactive: #000000;
$secondary-label: lighten($secondary, 20%);

$positive: #54b26c;
$negative: #ed5a5a;


$button-color: $link;

$header-color: #222222;
$background-color: white;

$border-color: #e1e1e1;

